* {
    box-sizing: border-box;
}


body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #f2f6f9;
    height: 100%;
}

html,
body,
#potato,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 60%;
}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
}

.movie img {
    position: relative;
    top: -30px;
    max-width: 120px;
    margin-right: 30px;
}

.movie h3,
.movie h5 {
    margin: 0;
    font-weight: 300;
}

.movie h3 {
    margin-bottom: 10px;
    font-size: 24px;
}

.movie .movie__genres,
.movie .movie__year {
    opacity: .5;
}